import {request} from 'network/request'

export function postProjectData (parameter) {
  return request({
    url: '/project',
    method: 'post',
    data: parameter
  })
}

export function getProjectDataAll () {
  return request({
    url: '/GetProjectDataAll',
    method: 'get'
  })
}

export function getProjectDataOne (parameter) {
  return request({
    url: '/GetProjectDataOne',
    method: 'post',
    data: parameter
  })
}

export function searchProjectData (parameter) {
  return request({
    url: '/SearchProjectData',
    method: 'post',
    data: parameter
  })
}

export function getTimeLineData (parameter) {
  return request({
    url: '/GetTimeLineData',
    method: 'post',
    data: parameter
  })
}
