<script>
import {getProjectDataAll, searchProjectData, getTimeLineData} from 'api/projects'
import PageHeader from 'components/common/menu/PageHeader'


export default {
  name: 'ProjectList',
  components: {
    PageHeader
  },
  data() {
    return {
      ruleForm: {
        ProjectName: '',
        ProjectCode: '',
        CustomerName: '',
        CreateDate: '',
        Is_Delete: false
      },
      ProjectName: '',
      timeLineVisible: false,
      tableData: [],
      timeLineData: [],
      search: ''
    }
  },
  created: function () {
    getProjectDataAll().then(res => {
      this.tableData = res.data.data
    }).catch(err => {
      console.log(err)
    })
  },
  methods: {
    searchProjectData() {
      let values = {}
      values['ProjectCode'] = this.ruleForm.ProjectCode
      values['ProjectName'] = this.ruleForm.ProjectName
      values['CustomerName'] = this.ruleForm.CustomerName
      values['Is_Delete'] = this.ruleForm.Is_Delete
      searchProjectData(values).then(res => {
        this.tableData = res.data.data
      }).catch(err => {
        console.log(err)
      })
    },
    getTimeLineData(ProjectCode) {
      let values = {}
      values['ProjectCode'] = ProjectCode
      getTimeLineData(values).then(res => {
        if (res.data.code === '200') {
          this.timeLineData = res.data.data
          this.timeLineVisible = true
        }
      }).catch(err => {
        console.log(err)
      })
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.searchProjectData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    createBtn() {
      this.$router.push({path: 'Projectform'})
    },
    handleEdit(index, row) {
      console.log(row);
      this.$router.push({path: 'Projectform', query: {ProjectCode: row.ProjectCode}})
    },
    handleDelete(index, row) {
      console.log(row.ProjectCode)
      this.$router.push({path: 'Projectform', query: {ProjectCode: row.ProjectCode}})
    },
    timeLine(index, row) {
      this.getTimeLineData(row.ProjectCode)
    },
    getPdffile() {
      console.log('PDF')
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

